<template>
  <div class="main quality-info">
<!--    <div class="main-search">-->
<!--      <div class="search-item">-->
<!--        <span class="item-left">文件名</span>-->
<!--        <span class="item-right">-->
<!--          <el-input placeholder="输入查询的字段" style="width: 200px;"></el-input>-->
<!--        </span>-->
<!--      </div>-->
<!--      <button type="button" class="btn-search" style="float: left;">查询</button>-->
<!--      <div class="search-item">-->
<!--        <button type="button" class="btn-search" style="float: left;" @click="selectAllFunc">-->
<!--          {{view.visible?'全选':'取消全选'}}-->
<!--        </button>-->
<!--      </div>-->
<!--    </div>-->
    <div class="main_popup scrollbar">
      <ul>
        <li @click="selectStyle(item)" v-for="(item, index) in dataList" :class="{'active':item.active}">
          <span :title="item.memo" v-if="dataId == '1'">{{item.memo}}</span>
          <span v-else>{{item.memo}}</span>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue'
    export default {
      name: "dialogBox",
      props: ['dataList','dataId'],
      data(){
        return {
          active: false,
          view:{
            visible:true,    //是否可见
          },
        }
      },
      methods:{
        selectAllFunc() {
          this.view.visible=!this.view.visible;
          var lis = document.getElementsByTagName('li');
          for (var j = 0; j < lis.length; j++){
            if (lis[j].className == 'active') {
              lis[j].className = '';
            }else {
              lis[j].className = 'active';
            }
          }
        },
        complete(){
          var model = [];
          var modelName=[],modelValue=[];
          for(var i=0;i<this.dataList.length;i++){
            if(this.dataList[i].active){
              modelName.push(this.dataList[i].memo);
              modelValue.push(this.dataList[i].id);
            }
          }
          model.push(modelName);
          model.push(modelValue);
          return model;
        },
        selectStyle (item, index) {
          if(item.active){
            Vue.set(item,'active',false)
          }else{
            Vue.set(item,'active',true)
          }
        }
      }
    }
</script>

<style scoped>
  @import "../assets/css/pages/main.css";
  .main_popup{
    max-height: 376px;
    overflow-y: scroll;
    padding: 20px 20px 0 32px;
  }
  .main-popup::-webkit-scrollbar-track{
    background-color: #007bc4;
  }
  .main-popup::-webkit-scrollbar-thumb{
    background-color: #007bc4;
  }
  .scrollbar::-webkit-scrollbar {/*滚动条整体样式*/
        width: 10px;     /*高宽分别对应横竖滚动条的尺寸*/
        height: 1px;
    }
  .scrollbar::-webkit-scrollbar-thumb {/*滚动条里面小方块*/
        border-radius: 10px;
         -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0.2);
        background: #007bc4;
    }
  .scrollbar::-webkit-scrollbar-track {/*滚动条里面轨道*/
        -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0.2);
        border-radius: 10px;
        background: #EDEDED;
    }
  .main_popup ul{
    overflow: hidden;
  }
  .main_popup ul li{
    float: left;
    width: 120px;
    height: 30px;
    text-align: center;
    line-height: 30px;
    font-size: 12px;
    color: #666666;
    border: 1px solid #eaeaea;
    border-radius: 3px;
    margin-right:15px;
    margin-bottom:12px;
    cursor: pointer;
    overflow:hidden;
    position: relative;
  }
  .main_popup ul li span{
    display: inline-block;
    width:100%;
    height:100%;
    overflow:hidden;
    -moz-user-select:none; /*火狐*/
    -webkit-user-select:none; /*webkit浏览器*/
    -ms-user-select:none; /*IE10*/
    user-select:none;
  }
  .main_popup ul li.active{
    background-color: #007bc4;
    color: #ffffff;
  }
  .dialog-footer{
    text-align:right
  }
  .el-button{
    width: 100%;
    border: none;
    padding: 0;
  }
</style>



